html, body {
  background: var(--bg);
  color: var(--text);
  font-family: var(--font-sans);
  margin: 0;
  padding: 0;
  min-height: 100vh;
}
.header {
  padding: 2rem 0 1rem 0;
  text-align: center;
}
.nav {
  display: flex;
  justify-content: center;
  gap: 2rem;
}
.nav-link {
  position: relative;
  text-decoration: none;
  color: var(--text);
  font-weight: 500;
  padding: 0.25rem 0;
  transition: color 0.2s;
}
.nav-link:hover,
.nav-link:focus {
  color: var(--accent);
}
.nav-underline {
  display: block;
  position: absolute;
  left: 0;
  bottom: -2px;
  width: 100%;
  height: 2px;
  background: var(--accent);
  transform: scaleX(0);
  transition: transform 0.3s cubic-bezier(.4,0,.2,1);
}
.nav-link:hover .nav-underline,
.nav-link:focus .nav-underline {
  transform: scaleX(1);
}
.main-content {
  max-width: 700px;
  margin: 3rem auto 0 auto;
  padding: 0 1rem;
}
section {
  margin-bottom: 3rem;
}
h1, h2, h3 {
  font-family: var(--font-sans);
  font-weight: 700;
  margin-bottom: 0.5rem;
}
ul {
  padding-left: 1.2rem;
}
.modern-header {
  text-align: center;
  margin-bottom: 2.5rem;
}
.modern-title {
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
}
.modern-contact {
  color: #555;
  font-size: 1.1rem;
}
.modern-link {
  color: var(--accent);
  text-decoration: none;
  transition: color 0.2s;
}
.modern-link:hover {
  text-decoration: underline;
}
.modern-cards {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}
@media (min-width: 700px) {
  .modern-cards {
    flex-direction: row;
    flex-wrap: wrap;
    gap: 2rem;
  }
}
.modern-card {
  background: #fff;
  border-radius: 1rem;
  box-shadow: 0 2px 16px 0 rgba(0,0,0,0.06);
  padding: 1.2rem 1.5rem;
  min-width: 260px;
  flex: 1 1 300px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.modern-card-header {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 0.5rem;
}
.modern-card-title {
  font-weight: 600;
  font-size: 1.1rem;
  display: block;
}
.modern-card-location {
  color: #888;
